	module.exports = {"data":{"markdownRemark":{"html":"<p>ドキュメントの山を並べ替え、夏にH Junによって書かれた手紙を見て、私は非常にオーラを感じる：</p>\n<p>\"それは非常に孤独にしかならない。南の小さな町の小さな窓の前に一人の人間が立っている。毎日、雨のカーテンを風が吹いているのを見て、豆の雨滴を見ている。フラワーベッドのゆるい土と飛ぶ花の繊細な翼の中で \"\"日々は往復して衰退しています。毎日、複雑な思い出を整理するために黙り強な瞑想をしています。時々、私は思考の思考を諦め、心と身体を伸ばし、神聖な音楽の音に夢中になることがあります。空の谷の声が心配のない、トラブルのない、苛立ちと無知につながるようにしましょう。いいえ、私の領域。</p>\n<p>「過去のクラスメートを考えれば、皆は生き残るために急いでいる。私はまだ天国の平和を楽しんでいるだけだ。私はゆっくりと竹のベンチに座って、涼しく、お茶を飲み、セージの本を読んで、本当にデイドリームをする。私はこれらの貴重な時代を大切にしなければならないと感じています。</p>\n<p>人々は自給自足、時には幸福の最大の源を知っており、内なる平和はすべての満足よりも優れています。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0171.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0171.md","frontmatter":{"title":"10月29日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0171.md absPath of file >>> MarkdownRemark"}}

